<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">腾讯IM客服（uniapp集成）</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                引用腾讯IM客服即TUIKit，是基于腾讯云 IM SDK 的一款 UI 组件库，它提供了一些通用的 UI 组件，包含会话、聊天、群组、音视频通话等功能。
            </div>
            <img src="../../../static/images/tim.png" width="1000" >
        </div>

        <div class="h-blocks">
            <div class="h-title">1.开发环境要求</div>
            <div class="hb-a">
                <p>HBuilderX</p>
                <p>Vue 3</p>
                <p>TypeScript</p>
                <p>sass（sass-loader 版本 ≤ 10.1.1）</p>
                <p>node（12.13.0 ≤ node 版本 ≤ 17.0.0, 推荐使用 Node.js 官方 LTS 版本 16.17.0）</p>
                <p>npm（版本请与 node 版本匹配）</p>
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">2.创建项目 （已有项目可忽略）</div>
            <div class="hb-a">
                <p><img src="../../../static/images/tim2.png" width="1000" ></p>
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">3.下载并引入 TUIKit</div>
            <div class="hb-a">
                <p>通过 npm 方式下载 TUIKit 并集成组件。有2种集成方案，主包集成（不推荐），分包集成（推荐）。</p>
                <p><strong>分包集成：</strong></p>
                <p>a.npm下载。</p>
                <div v-highlight>
                        <pre>
                            <code >
                                # macOS
                                npm i @tencentcloud/chat-uikit-uniapp
                                ----------------------------------------------
                                mkdir -p ./TUIKit && cp -r ./node_modules/@tencentcloud/chat-uikit-uniapp/ ./TUIKit && mv ./TUIKit/debug ./debug
                                ----------------------------------------------
                                # windows
                                npm i @tencentcloud/chat-uikit-uniapp
                                ----------------------------------------------
                                xcopy .\node_modules\@tencentcloud\chat-uikit-uniapp .\TUIKit /i /e
                                ----------------------------------------------
                                move .\TUIKit\debug .\debug
                            </code>
                        </pre>
                </div>
                <p>App.vue文件</p>
                <div v-highlight>
                        <pre>
                            <code >
                                import { genTestUserSig, aegisID } from "./pages/TUIKit/debug/index.js";
                                import { TIM, TIMUploadPlugin, Aegis } from './pages/TUIKit/debug/tim.js';
                                const aegis = new Aegis({
                                    id: aegisID, // 项目key
                                    reportApiSpeed: true, // 接口测速
                                });
                                uni.$aegis = aegis;

                                const config = {
                                    userID: "", //User ID
                                    SDKAppID: 0, // Your SDKAppID
                                    secretKey: "", // Your secretKey
                                };
                                uni.$chat_SDKAppID = config.SDKAppID;
                                const userSig = genTestUserSig(config).userSig;
                                // 创建 sdk 实例
                                uni.$TUIKit = TIM.create({
                                    SDKAppID: uni.$chat_SDKAppID,
                                });
                                uni.$TIM = TIM;
                                // 注册文件上传插件
                                // #ifdef MP-WEIXIN || H5
                                uni.$TUIKit.registerPlugin({
                                    "tim-upload-plugin": TIMUploadPlugin,
                                });
                                // #endif
                                // #ifdef APP-PLUS
                                uni.$TUIKit.registerPlugin({
                                    "cos-wx-sdk": TIMUploadPlugin,
                                });
                                // #endif
                                export default {
                                    onLaunch: function () {
                                        this.bindTIMEvent();
                                        this.login();
                                    },
                                    methods: {
                                        login() {
                                            // login TUIKit
                                            uni.$TUIKit.login({ userID: config.userID, userSig }).then((res) => {
                                                // sdk 初始化，当 sdk 处于ready 状态，才可以使用API，文档
                                                uni.showLoading({
                                                    title: "初始化...",
                                                });
                                            });
                                        },
                                        bindTIMEvent() {
                                            uni.$TUIKit.on(uni.$TIM.EVENT.SDK_READY, this.handleSDKReady);
                                            uni.$TUIKit.on(uni.$TIM.EVENT.SDK_NOT_READY,this.handleSDKNotReady);
                                            uni.$TUIKit.on(uni.$TIM.EVENT.KICKED_OUT, this.handleKickedOut);
                                        },
                                        // sdk ready 以后可调用 API
                                        handleSDKReady(event) {
                                            uni.setStorageSync('$chat_SDKReady', true);
                                            uni.hideLoading();
                                        },
                                        handleSDKNotReady(event) {
                                            uni.showToast({
                                                title: "SDK 未完成初始化",
                                            });
                                        },

                                        handleKickedOut(event) {
                                            uni.clearStorageSync();
                                            uni.showToast({
                                                title: `${this.kickedOutReason(event.data.type)}被踢出，请重新登录。`,
                                                icon: "none",
                                            });
                                        },

                                        kickedOutReason(type) {
                                            switch (type) {
                                                case uni.$TIM.TYPES.KICKED_OUT_MULT_ACCOUNT:
                                                    return "由于多实例登录";
                                                case uni.$TIM.TYPES.KICKED_OUT_MULT_DEVICE:
                                                    return "由于多设备登录";
                                                case uni.$TIM.TYPES.KICKED_OUT_USERSIG_EXPIRED:
                                                    return "由于 userSig 过期";
                                                case uni.$TIM.TYPES.KICKED_OUT_REST_API:
                                                    return "由于 REST API kick 接口踢出";
                                                default:
                                                    return "";
                                            }
                                        },
                                    },
                                };

                            </code>
                        </pre>
                </div>
                <p>pages.json文件</p>
                <div v-highlight>
                        <pre>
                            <code >
                                {
                                  "pages": [
                                    {
                                      "path": "pages/index/index" // 自己项目首页
                                    }
                                  ],
                                  "subPackages": [
                                    {
                                      "root": "TUIKit",
                                      "pages": [
                                        {
                                          "path": "TUIPages/TUIConversation/index",
                                          "style": {
                                            "navigationBarTitleText": "云通信 IM",
                                          }
                                        },
                                        {
                                          "path": "TUIPages/TUIConversation/create",
                                          "style": {
                                            "navigationBarTitleText": "选择联系人",
                                            "app-plus": {
                                              "scrollIndicator": "none"
                                            }
                                          }
                                        },
                                        {
                                          "path": "TUIPages/TUIChat/index",
                                          "style": {
                                            "navigationBarTitleText": "云通信 IM",
                                            "app-plus": {
                                              "scrollIndicator": "none", //当前页面不显示滚动条
                                              "softinputNavBar": "none", // App平台在iOS上，webview中的软键盘弹出时，默认在软键盘上方有一个横条，显示着：上一项、下一项和完成等按钮
                                              "bounce": "none", // 页面回弹
                                            }
                                          }
                                        },
                                        {
                                          "path": "TUIPages/TUIChat/components/message-elements/video-play",
                                          "style": {
                                            "navigationBarTitleText": "云通信 IM",
                                            "app-plus": {}
                                          }
                                        },
                                        {
                                          "path": "TUIPages/TUIGroup/index",
                                          "style": {
                                            "navigationBarTitleText": "群管理",
                                            "app-plus": {
                                              "scrollIndicator": "none"
                                            }
                                          }
                                        },
                                        {
                                          "path": "TUIPages/TUIGroup/memberOperate",
                                        }
                                      ]
                                    }
                                  ],
                                    "globalStyle": {
                                        "navigationBarTextStyle": "black",
                                        "navigationBarTitleText": "uni-app",
                                        "navigationBarBackgroundColor": "#F8F8F8",
                                        "backgroundColor": "#F8F8F8"
                                      }
                                    }
                            </code>
                        </pre>
                </div>
                <p>成功后目录结构如图所示：</p>
                <p><img src="../../../static/images/tim3.png" ></p>
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">4.小程序管理后台配置</div>
            <div class="hb-a">
                <p><img src="../../../static/images/tim4.png" width="1000" ></p>

            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'wltxtlinkhref',
        data() {
            return {

            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{

        }
    }
</script>
<style scoped>
    .h-headtitle{height: auto;}
    .download-files{color:#333;}
    .download-files:hover{text-decoration: underline;}
</style>



